js
 	var dom = document.getElementById("box");
	var myChart = echarts.init(dom);
 	var symbolSize = 20;
	//  data 
	var data = [[15, 0], [-50, 10], [-56.5, 20], [-46.5, 30], [-22.1, 40]];
	myChart.setOption({
	    xAxis: {
	        min: -100,
	        max: 80,
	        type: 'value',
	        axisLine: {onZero: false}  //0
	    },
	    dataZoom: [
	        {
	            type: 'slider',
	            xAxisIndex: 0,
	            filterMode: 'empty'
	        },
	        {
	            type: 'slider',
	            yAxisIndex: 0,
	            filterMode: 'empty'
	        },
	        {
	            type: 'inside',
	            xAxisIndex: 0,
	            filterMode: 'empty'
	        },
	        {
	            type: 'inside',
	            yAxisIndex: 0,
	            filterMode: 'empty'
	        }
	    ],
	    yAxis: {
	        min: -30,
	        max: 60,
	        type: 'value',
	        axisLine: {onZero: false}  
	    },
	    visualMap: {   //
	        min: -150,   // 
	        max: 200,  //
	        calculable: true,  //
	        inRange: {  //   
	            color: ['#50a3ba', '#eac736', 'red']  //
	        },
	        textStyle: { //
	            color: 'red'  
	        }
	    },
	    tooltip: { //
	        trigger: 'item',  //    item 
	                        //'axis'    
	                        //'none'    
	        formatter: function (params) {  // hover ,params 
	            return params.data[0]+"/"+params.data[1];
	        }
	    },
	    series: [  //
	        {
	            id: 'a', //id
	            type: 'line',
	            smooth: true,   //
	            symbolSize: symbolSize*2, //  symbolSize 
	            data: data  
	        }
	    ]
	});
    myChart.on('dataZoom', updatePosition); //
	window.addEventListener('resize', updatePosition);  //
	myChart.setOption({  //graphic
    //  graphic component type  'circle'  graphic elements
    //  echarts.util.map  Array.prototype.map  es5 
    //  map  data 
    graphic: echarts.util.map(data, function (dataItem, dataIndex) { //mao data
        return {
            // 'circle'  graphic element 
            type: 'circle',  //

            shape: {
                // 
                r: symbolSize / 2
            },
            //  transform position: [x, y]  [x, y] 
            //  convertToPixel  API 
            position: myChart.convertToPixel('grid', dataItem),

            // 
            invisible: false,
            // 
            draggable: true,
            //  z  z-index
            z: 100,
            // 
            //  echarts.util.curry  onPointDragging
            //  dataIndex 
            ondrag: echarts.util.curry(onPointDragging, dataIndex)
        };

    })

});
	// myChart.convertFromPixel   myChart.convertToPixel 
	// myChart.convertToPixel data
	// myChart.convertFromPixel data
	 // 
	//  data  data 
	function onPointDragging(dataIndex) {
	    //  data  data
	    //  this this.position 
	    data[dataIndex] = myChart.convertFromPixel('grid', this.position);
	    //  data
	    myChart.setOption({
	        series: [{
	            id: 'a',  //id
	            data: data
	        }]
	    });
	}
     function updatePosition() {  //
	    //  setOption 
	    myChart.setOption({
	        graphic: echarts.util.map(data, function (item, dataIndex) {
	            return {
	                position: myChart.convertToPixel('grid', item)
	            };
	        })
	    });
	}
		</pre>
